import { getDestinations } from '../mock/destination-mock.js';

export default class DestinationsModel {
  #destinations = getDestinations();

  get destinations() {
    return this.#destinations;
  }

  getRouteOfTrip(destinationIds) {
    const routeities = [];
    const numberOfCities = destinationIds.length;
    switch(numberOfCities.toString()){
      case '1':
        routeities.push(this.#getNamePoint(destinationIds[0]));
        break;
      case '2':
        routeities.push(this.#getNamePoint(destinationIds[0]));
        routeities.push(this.#getNamePoint(destinationIds[1]));
        break;
      case '3':
        routeities.push(this.#getNamePoint(destinationIds[0]));
        routeities.push(this.#getNamePoint(destinationIds[1]));
        routeities.push(this.#getNamePoint(destinationIds[2]));
        break;
      default:
        routeities.push(this.#getNamePoint(destinationIds[0]));
        routeities.push(this.#getNamePoint(''));
        routeities.push(this.#getNamePoint(destinationIds[destinationIds.length - 1]));
    }
    return routeities;
  }

  #getNamePoint(idDestination) {
    return this.#destinations.find((destination) => destination.id === idDestination)?.name;
  }
}

